<template>
  <component :is="tag" :class="className">
    <slot />
  </component>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'MDBCol',
  props: {
    tag: {
      type: String,
      default: 'div'
    },
    col: {
      type: String
    },
    sm: {
      type: String
    },
    md: {
      type: String
    },
    lg: {
      type: String
    },
    xl: {
      type: String
    },
    offset: {
      type: String
    },
    offsetSm: {
      type: String
    },
    offsetMd: {
      type: String
    },
    offsetLg: {
      type: String
    },
    offsetXl: {
      type: String
    },
    auto: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const className = computed(() => {
      return [
        props.col ? `col-${props.col}` : '',
        props.sm ? `col-sm-${props.sm}` : '',
        props.md ? `col-md-${props.md}` : '',
        props.lg ? `col-lg-${props.lg}` : '',
        props.xl ? `col-xl-${props.xl}` : '',
        !props.col && !props.sm && !props.md && !props.lg && !props.xl
          ? 'col'
          : '',
        props.offset ? `offset-${props.offset}` : '',
        props.offsetSm ? `offset-sm-${props.offsetSm}` : '',
        props.offsetMd ? `offset-md-${props.offsetMd}` : '',
        props.offsetLg ? `offset-lg-${props.offsetLg}` : '',
        props.offsetXl ? `offset-xl-${props.offsetXl}` : '',
        props.auto ? 'col-auto' : ''
      ]
    })

    return {
      className,
      props
    }
  }
}
</script>
